.textHeading1 {
  margin: 0;
  position: relative;
  font-size: inherit;
  letter-spacing: -0.02em;
  line-height: 29px;
  font-weight: 600;
  font-family: inherit;
}
.textHeading {
  flex-direction: row;
}
.cardGridReviews,
.habitualMobileViewCheckout,
.productCards,
.textHeading {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.productCards {
  align-self: stretch;
  overflow: hidden;
  flex-direction: row;
  gap: var(--gap-29xl);
}
.cardGridReviews,
.habitualMobileViewCheckout {
  background-color: var(--background-default-default);
  flex-direction: column;
}
.cardGridReviews {
  width: var(--device-width);
  padding: var(--padding-5xl) var(--padding-xs) var(--padding-15xl)
    var(--space-1600);
  box-sizing: border-box;
  gap: var(--space-1200);
  max-width: 104%;
  flex-shrink: 0;
  text-align: left;
  font-size: var(--heading-size);
  color: var(--text-default-default);
  font-family: var(--body-strong);
}
.habitualMobileViewCheckout {
  width: 100%;
  position: relative;
  overflow: hidden;
  line-height: normal;
  letter-spacing: normal;
}
