.heading,
.subheading {
  align-self: stretch;
  position: relative;
}
.heading {
  margin: 0;
  font-size: inherit;
  letter-spacing: -0.02em;
  line-height: 29px;
  font-weight: 600;
  font-family: inherit;
}
.subheading {
  font-size: var(--subheading-size);
  line-height: 120%;
  color: var(--text-default-secondary);
}
.textContentHeading {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--space-200);
}
.helloWorld {
  width: 100%;
  border: 0;
  outline: 0;
  font-family: var(--body-strong);
  font-size: var(--body-strong-size);
  background-color: transparent;
  height: 16px;
  flex: 1;
  position: relative;
  line-height: 100%;
  color: var(--text-default-tertiary);
  text-align: left;
  display: inline-block;
  min-width: 130px;
  white-space: nowrap;
  padding: 0;
}
.input,
.starIcon {
  overflow: hidden;
  flex-shrink: 0;
}
.input {
  height: 41px;
  width: 250px;
  border-radius: var(--radius-200);
  background-color: var(--background-default-default);
  border: 1px solid var(--border-default-default);
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--space-300) var(--padding-mini) var(--space-300)
    var(--space-400);
}
.starIcon {
  height: 16px;
  width: 16px;
  position: relative;
  display: none;
  min-height: 16px;
}
.button1 {
  position: relative;
  font-size: var(--body-strong-size);
  line-height: 100%;
  font-family: var(--body-strong);
  color: var(--background-default-secondary);
  text-align: left;
  display: inline-block;
  min-width: 53px;
}
.button,
.xIcon {
  overflow: hidden;
  flex-shrink: 0;
}
.xIcon {
  height: 16px;
  width: 16px;
  position: relative;
  display: none;
  min-height: 16px;
}
.button {
  cursor: pointer;
  border: 1px solid var(--background-brand-default);
  padding: var(--padding-3xs) var(--padding-2xs);
  background-color: var(--background-brand-default);
  width: 77px;
  border-radius: var(--radius-200);
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--space-200);
}
.newsletter,
.pageNewsletter {
  align-self: stretch;
  display: flex;
  justify-content: flex-start;
}
.newsletter {
  height: 40px;
  flex-direction: row;
  align-items: flex-end;
  padding: 0;
  box-sizing: border-box;
  gap: var(--space-300);
}
.pageNewsletter {
  background-color: var(--background-default-default);
  flex-direction: column;
  align-items: center;
  padding: var(--space-600);
  gap: var(--space-600);
  text-align: center;
  font-size: var(--heading-size);
  color: var(--text-default-default);
  font-family: var(--body-strong);
}
