.heartIcon {
  height: 16px;
  width: 16px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.iconContainer {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end;
}
.imageContainer,
.imageIcon {
  align-self: stretch;
  flex: 1;
  overflow: hidden;
}
.imageIcon {
  position: relative;
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}
.imageContainer {
  border-radius: var(--br-5xs);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.productName {
  align-self: stretch;
  position: relative;
  font-size: var(--label-14px-regular-size);
  line-height: 18px;
  font-weight: 500;
}
.starIcon {
  height: 10px;
  width: 10px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.reviewScore {
  position: relative;
  line-height: 125%;
  font-weight: 500;
  display: inline-block;
  min-width: 26px;
}
.review {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-9xs);
  color: var(--neutral-n800);
}
.productInfo {
  align-self: stretch;
  position: relative;
  line-height: 150%;
  color: var(--neutral-n700);
}
.productDetails {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-9xs);
}
.price {
  align-self: stretch;
  position: relative;
  font-size: var(--label-14px-regular-size);
  line-height: 18px;
  font-weight: 500;
  color: var(--neutral-n600);
  white-space: nowrap;
}
.informationContainer,
.productCard {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.informationContainer {
  align-self: stretch;
  align-items: flex-start;
  gap: var(--gap-5xs);
}
.productCard {
  height: 361px;
  width: 270px;
  border-radius: var(--br-3xs);
  background-color: var(--neutral-n100);
  overflow: hidden;
  flex-shrink: 0;
  align-items: center;
  padding: var(--padding-xs);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--body-12px-regular-size);
  color: var(--text-default-default);
  font-family: var(--body-strong);
}
