.imageRectIcon {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: contain;
}
.image {
  height: 173px;
  width: 187px;
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: var(--padding-22xl-5) var(--padding-19xl);
  box-sizing: border-box;
  background-image: url(/public/image@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  min-width: 187px;
}
.controller,
.dualsenseWireless {
  margin: 0;
}
.textHeading1 {
  margin: 0;
  position: relative;
  font-size: inherit;
  letter-spacing: -0.02em;
  line-height: 120%;
  font-weight: 600;
  font-family: inherit;
}
.textHeading {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-10xs) 0 0;
}
.tag1,
.xIcon {
  height: 16px;
  position: relative;
}
.tag1 {
  flex: 1;
  line-height: 100%;
  display: inline-block;
}
.xIcon {
  width: 16px;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.tag {
  width: 44px;
  border-radius: var(--radius-200);
  background-color: var(--background-positive-secondary);
  display: none;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--space-200);
  box-sizing: border-box;
  gap: var(--space-200);
}
.currency {
  margin: 0;
  font-size: inherit;
  font-weight: 700;
  font-family: inherit;
  display: inline-block;
  min-width: 32px;
}
.currency,
.currency1 {
  position: relative;
  letter-spacing: -0.02em;
  line-height: 58px;
}
.price1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.month {
  width: 52px;
  position: relative;
  font-size: var(--label-14px-regular-size);
  line-height: 140%;
  display: none;
}
.textPrice {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: flex-start;
  gap: var(--space-200);
  font-size: var(--title-page-size);
  color: var(--text-default-default);
}
.price,
.title {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.price {
  justify-content: center;
  gap: var(--space-100);
  font-size: var(--body-strong-size);
  color: var(--text-positive-on-positive-secondary);
}
.title {
  justify-content: flex-start;
  gap: var(--space-400);
}
.text1 {
  flex: 1;
  position: relative;
  line-height: 140%;
}
.body,
.text {
  align-self: stretch;
  justify-content: flex-start;
}
.text {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  font-size: var(--body-strong-size);
  color: var(--text-default-secondary);
}
.body {
  flex-direction: column;
  padding: 0 var(--padding-3xl) 0 0;
  gap: var(--space-400);
}
.body,
.column,
.section {
  display: flex;
  align-items: flex-start;
}
.column {
  flex: 1;
  flex-direction: column;
  justify-content: center;
  min-width: 161px;
}
.section {
  width: 499px;
  flex-direction: row;
  justify-content: flex-start;
  gap: var(--space-1600);
  min-width: 499px;
  max-width: 100%;
}
.cartTotals,
.subtotal1,
.subtotalValue {
  position: relative;
  letter-spacing: -0.02em;
  line-height: 29px;
  font-weight: 600;
  display: inline-block;
}
.cartTotals {
  margin: 0;
  font-size: inherit;
  font-family: inherit;
  min-width: 123px;
}
.subtotal1,
.subtotalValue {
  flex-shrink: 0;
}
.subtotal1 {
  margin: 0;
  font-size: inherit;
  font-family: inherit;
  min-width: 99px;
}
.subtotalValue {
  min-width: 80px;
  white-space: nowrap;
}
.subtotal {
  width: 143px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-274xl);
}
.freeShipping,
.shipping1 {
  margin: 0;
  position: relative;
  font-size: inherit;
  letter-spacing: -0.02em;
  line-height: 29px;
  font-family: inherit;
}
.shipping1 {
  align-self: stretch;
  font-weight: 600;
}
.freeShipping {
  font-weight: 400;
}
.cartDetails,
.shipping {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.shipping {
  align-self: stretch;
  gap: var(--gap-6xs);
}
.cartDetails {
  width: 151px;
  padding: 0 0 var(--padding-smi);
  box-sizing: border-box;
  gap: var(--gap-xl);
}
.changeAddress,
.shippingToCalifonia {
  margin: 0;
  position: relative;
  font-size: inherit;
  letter-spacing: -0.02em;
  line-height: 29px;
  font-weight: 400;
  font-family: inherit;
}
.changeAddress {
  font-weight: 600;
}
.location {
  width: 477px;
  height: 108px;
  border-bottom: 2px solid var(--color-black);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-3xs);
  max-width: 334%;
  flex-shrink: 0;
}
.total,
.totalCost {
  position: relative;
  letter-spacing: -0.02em;
  line-height: 29px;
  font-weight: 600;
  display: inline-block;
  flex-shrink: 0;
}
.total {
  margin: 0;
  font-size: inherit;
  font-family: inherit;
  min-width: 56px;
}
.totalCost {
  min-width: 80px;
  white-space: nowrap;
}
.totalValue {
  width: 141px;
  flex-direction: row;
  gap: var(--gap-318xl);
}
.cart,
.totalValue,
.totalValueWrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.totalValueWrapper {
  flex-direction: row;
  padding: 0 0 0 var(--padding-11xs);
}
.cart {
  width: 143px;
  flex-direction: column;
  padding: 0;
  box-sizing: border-box;
  gap: var(--gap-xl);
}
.starIcon {
  height: 16px;
  width: 16px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
  min-height: 16px;
}
.button1 {
  position: relative;
  line-height: 100%;
}
.xIcon1 {
  height: 16px;
  width: 16px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
  min-height: 16px;
}
.button,
.pageProduct,
.sidebar {
  display: flex;
  align-items: flex-start;
}
.button {
  align-self: stretch;
  border-radius: var(--radius-200);
  background-color: var(--background-brand-default);
  border: 1px solid var(--background-brand-default);
  overflow: hidden;
  flex-direction: row;
  justify-content: center;
  padding: var(--padding-3xs) var(--padding-xl);
  gap: var(--space-200);
  font-size: var(--body-strong-size);
  color: var(--background-default-secondary);
}
.pageProduct,
.sidebar {
  justify-content: flex-start;
  box-sizing: border-box;
  gap: var(--gap-34xl);
}
.sidebar {
  flex: 1;
  border: 1px solid var(--color-black);
  flex-direction: column;
  padding: var(--padding-9xl) var(--padding-10xl);
  min-width: 349px;
  max-width: 100%;
}
.pageProduct {
  width: 1200px;
  background-color: var(--background-default-default);
  flex-direction: row;
  padding: var(--space-1600) var(--padding-28xl) var(--space-1600)
    var(--space-1600);
  min-width: 240px;
  max-width: 153%;
  flex-shrink: 0;
  z-index: 1;
  text-align: left;
  font-size: var(--heading-size);
  color: var(--text-default-default);
  font-family: var(--body-strong);
}
@media screen and (max-width: 1050px) {
  .currency,
  .currency1 {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
  .section {
    flex: 1;
  }
  .pageProduct {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 750px) {
  .imageRectIcon {
    align-self: stretch;
    width: auto;
  }
  .image {
    flex: 1;
  }
  .section {
    flex-wrap: wrap;
    gap: var(--space-1600);
    min-width: 100%;
  }
  .subtotal {
    gap: var(--gap-127xl);
  }
  .totalValue {
    gap: var(--gap-149xl);
  }
  .pageProduct,
  .sidebar {
    gap: var(--gap-7xl);
    box-sizing: border-box;
  }
  .sidebar {
    padding-top: var(--padding-xl);
    padding-bottom: var(--padding-xl);
    min-width: 100%;
  }
  .pageProduct {
    padding: var(--space-1600) var(--padding-4xl) var(--space-1600)
      var(--space-1600);
  }
}
@media screen and (max-width: 450px) {
  .textHeading1 {
    font-size: var(--font-size-lgi);
    line-height: 23px;
  }
  .currency,
  .currency1 {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .cartTotals,
  .changeAddress,
  .freeShipping,
  .shipping1,
  .shippingToCalifonia,
  .subtotal1,
  .subtotalValue,
  .total,
  .totalCost {
    font-size: var(--font-size-lgi);
    line-height: 23px;
  }
}
