.subtitle,
.title {
  position: relative;
  line-height: 120%;
}
.title {
  margin: 0;
  align-self: stretch;
  font-size: inherit;
  letter-spacing: -0.03em;
  font-weight: 900;
  font-family: inherit;
}
.subtitle {
  width: 151px;
  font-size: var(--subtitle-size);
  display: none;
}
.textContentTitle {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--space-200);
}
.starIcon {
  height: 16px;
  width: 16px;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
  min-height: 16px;
}
.button1,
.starIcon,
.xIcon {
  position: relative;
}
.button1 {
  font-size: var(--body-strong-size);
  line-height: 100%;
  font-weight: 500;
  font-family: var(--body-strong);
  color: var(--background-default-default);
  text-align: left;
  display: inline-block;
  min-width: 87px;
  white-space: nowrap;
}
.xIcon {
  height: 16px;
  width: 16px;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
  min-height: 16px;
}
.button {
  border-radius: var(--radius-200);
  background-color: var(--text-default-default);
  border: 1px solid var(--border-neutral-secondary);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-3xs) var(--padding-2xs);
  gap: var(--space-200);
}
.starIcon1 {
  height: 16px;
  width: 16px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
  min-height: 16px;
}
.button3 {
  align-self: stretch;
  position: relative;
  font-size: var(--body-strong-size);
  line-height: 100%;
  font-family: var(--body-strong);
  color: var(--background-default-secondary);
  text-align: left;
}
.button2,
.xIcon1 {
  overflow: hidden;
  display: none;
}
.xIcon1 {
  height: 16px;
  width: 16px;
  position: relative;
  flex-shrink: 0;
  min-height: 16px;
}
.button2 {
  align-self: stretch;
  border-radius: var(--radius-200);
  background-color: var(--background-brand-default);
  border: 1px solid var(--background-brand-default);
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--space-300) var(--padding-2xs);
  gap: var(--space-200);
}
.buttonGroup,
.heroImage {
  display: flex;
  justify-content: center;
}
.buttonGroup {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  flex-direction: row;
  align-items: center;
  gap: var(--space-400);
}
.heroImage {
  width: var(--device-width);
  flex-direction: column;
  align-items: flex-start;
  padding: var(--space-4000) var(--padding-484xl) var(--space-4000)
    var(--padding-69xl);
  box-sizing: border-box;
  gap: var(--space-800);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  max-width: 104%;
  flex-shrink: 0;
  font-size: var(--font-size-46xl);
  color: var(--text-utilities-text-on-overlay);
}
.heading {
  margin: 0;
  position: relative;
  font-size: inherit;
  letter-spacing: -0.02em;
  line-height: 29px;
  font-weight: 600;
  font-family: inherit;
}
.subheading {
  width: 113px;
  position: relative;
  font-size: var(--subheading-size);
  line-height: 120%;
  color: var(--text-default-secondary);
  display: none;
}
.textContentHeading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 0 var(--padding-xl) 0 0;
  gap: var(--gap-5xs);
}
.doubleProductCards,
.panelImageDouble {
  align-self: stretch;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-29xl);
  max-width: 100%;
}
.doubleProductCards {
  display: grid;
  flex-direction: row;
  grid-template-columns: repeat(3, minmax(247px, 1fr));
}
.panelImageDouble {
  background-color: var(--background-default-default);
  display: flex;
  flex-direction: column;
  padding: var(--space-1600) var(--padding-base) var(--padding-75xl)
    var(--space-1600);
  box-sizing: border-box;
  opacity: 0.8;
}
.heading1 {
  margin: 0;
  position: relative;
  font-size: inherit;
  letter-spacing: -0.02em;
  line-height: 29px;
  font-weight: 600;
  font-family: inherit;
  display: inline-block;
  min-width: 83px;
}
.subheading1 {
  align-self: stretch;
  height: 24px;
  position: relative;
  font-size: var(--subheading-size);
  line-height: 120%;
  color: var(--text-default-secondary);
  display: none;
}
.textContentHeading1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 0 var(--padding-xl) 0 0;
  gap: var(--gap-5xs);
}
.panelImageDouble1,
.productCardGrid,
.productCardParent {
  align-self: stretch;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.productCardParent {
  display: grid;
  flex-direction: row;
  gap: var(--gap-29xl);
  grid-template-columns: repeat(3, minmax(247px, 1fr));
}
.panelImageDouble1,
.productCardGrid {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
.panelImageDouble1 {
  background-color: var(--background-default-default);
  padding: var(--space-1600) var(--padding-base) var(--padding-75xl)
    var(--space-1600);
  gap: var(--gap-29xl);
  opacity: 0.8;
}
.productCardGrid {
  padding: 0;
  text-align: left;
  font-size: var(--heading-size);
  color: var(--text-default-default);
  font-family: var(--body-strong);
}
@media screen and (max-width: 900px) {
  .title {
    font-size: var(--font-size-33xl);
    line-height: 62px;
  }
  .subtitle {
    font-size: var(--font-size-7xl);
    line-height: 31px;
  }
  .doubleProductCards,
  .productCardParent {
    justify-content: center;
    grid-template-columns: repeat(2, minmax(247px, 428px));
  }
}
@media screen and (max-width: 675px) {
  .heroImage {
    gap: var(--space-800);
    padding: var(--space-4000) var(--padding-106xl) var(--space-4000)
      var(--padding-3xl);
    box-sizing: border-box;
  }
  .doubleProductCards {
    gap: var(--gap-5xl);
    grid-template-columns: minmax(247px, 1fr);
  }
  .panelImageDouble {
    gap: var(--gap-5xl);
    padding-left: var(--space-1600);
    padding-top: var(--space-1600);
    padding-bottom: var(--space-1600);
    box-sizing: border-box;
  }
  .productCardParent {
    gap: var(--gap-5xl);
    grid-template-columns: minmax(247px, 1fr);
  }
  .panelImageDouble1 {
    gap: var(--gap-5xl);
    padding-left: var(--space-1600);
    padding-top: var(--space-1600);
    padding-bottom: var(--space-1600);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .title {
    font-size: var(--font-size-20xl);
    line-height: 47px;
  }
  .subtitle {
    font-size: var(--font-size-lgi);
    line-height: 23px;
  }
  .heroImage {
    padding-right: var(--space-4000);
    box-sizing: border-box;
  }
  .heading {
    font-size: var(--font-size-lgi);
    line-height: 23px;
  }
  .subheading {
    font-size: var(--body-strong-size);
    line-height: 19px;
  }
  .heading1 {
    font-size: var(--font-size-lgi);
    line-height: 23px;
  }
  .subheading1 {
    font-size: var(--body-strong-size);
    line-height: 19px;
  }
}
