.georgeOkoli {
  margin: 0;
}
.textHeading1 {
  margin: 0;
  height: 58px;
  position: relative;
  font-size: inherit;
  letter-spacing: -0.02em;
  line-height: 120%;
  font-weight: 600;
  font-family: inherit;
  display: inline-block;
}
.textHeading {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.tag1,
.xIcon {
  height: 16px;
  position: relative;
}
.tag1 {
  flex: 1;
  line-height: 100%;
  display: inline-block;
}
.xIcon {
  width: 16px;
  overflow: hidden;
  flex-shrink: 0;
}
.b,
.tag,
.xIcon {
  display: none;
}
.tag {
  width: 44px;
  border-radius: var(--radius-200);
  background-color: var(--background-positive-secondary);
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--space-200);
  box-sizing: border-box;
  gap: var(--space-200);
}
.b {
  width: 32px;
  position: relative;
  letter-spacing: -0.02em;
  line-height: 120%;
}
.b,
.b1,
.price1 {
  align-self: stretch;
}
.b1 {
  width: 132px;
  position: relative;
  letter-spacing: -0.02em;
  line-height: 120%;
  display: none;
}
.price1 {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.month {
  width: 52px;
  position: relative;
  font-size: var(--label-14px-regular-size);
  line-height: 140%;
  display: none;
}
.textPrice {
  width: 96px;
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: flex-start;
  gap: var(--space-200);
  font-size: var(--title-page-size);
  color: var(--text-default-default);
}
.price,
.title {
  flex: 1;
  flex-direction: column;
}
.price {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  gap: var(--space-100);
  font-size: var(--body-strong-size);
  color: var(--text-positive-on-positive-secondary);
}
.title {
  width: 246px;
  justify-content: flex-start;
  padding: 0 var(--padding-xl) 0 0;
  box-sizing: border-box;
  gap: var(--space-400);
}
.body,
.column,
.section,
.title {
  display: flex;
  align-items: flex-start;
}
.body {
  align-self: stretch;
  flex: 1;
  flex-direction: column;
  justify-content: flex-start;
}
.column,
.section {
  max-width: 100%;
}
.column {
  align-self: stretch;
  flex: 1;
  flex-direction: column;
  justify-content: center;
}
.section {
  height: 132px;
  width: 499px;
  flex-direction: row;
  justify-content: flex-start;
  min-width: 499px;
}
.cartTotals1 {
  margin: 0;
  font-size: inherit;
  font-family: inherit;
  min-width: 123px;
}
.cartTotals1,
.subTotalValues,
.subtotal {
  position: relative;
  letter-spacing: -0.02em;
  line-height: 29px;
  font-weight: 600;
  display: inline-block;
}
.subtotal {
  margin: 0;
  font-size: inherit;
  font-family: inherit;
  min-width: 99px;
}
.subTotalValues {
  min-width: 80px;
  white-space: nowrap;
}
.subTotal {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.freeShipping,
.shipping1 {
  margin: 0;
  position: relative;
  font-size: inherit;
  letter-spacing: -0.02em;
  line-height: 29px;
  font-family: inherit;
}
.shipping1 {
  align-self: stretch;
  font-weight: 600;
}
.freeShipping {
  font-weight: 400;
}
.cartTotals,
.shipping {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.shipping {
  width: 151px;
  gap: var(--gap-6xs);
}
.cartTotals {
  align-self: stretch;
  gap: var(--gap-xl);
}
.changeAddress,
.shippingToCalifonia {
  position: relative;
  letter-spacing: -0.02em;
  line-height: 29px;
}
.changeAddress {
  width: 187px;
  border: 0;
  outline: 0;
  font-weight: 600;
  font-family: var(--body-strong);
  font-size: var(--heading-size);
  background-color: transparent;
  height: 29px;
  line-height: 120%;
  color: var(--text-default-default);
  text-align: left;
  display: inline-block;
  padding: 0;
}
.addressDetails {
  align-self: stretch;
  height: 108px;
  border-bottom: 2px solid var(--color-black);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.total1,
.totalValues {
  position: relative;
  letter-spacing: -0.02em;
  line-height: 29px;
  font-weight: 600;
  display: inline-block;
}
.total1 {
  margin: 0;
  font-size: inherit;
  font-family: inherit;
  min-width: 56px;
}
.totalValues {
  min-width: 80px;
  white-space: nowrap;
}
.address,
.total,
.totalValue {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 100%;
}
.totalValue {
  flex: 1;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.address,
.total {
  align-self: stretch;
  justify-content: flex-start;
  padding: 0 var(--padding-11xs);
  box-sizing: border-box;
}
.address {
  flex-direction: column;
  padding: 0 0 var(--padding-xl);
  gap: var(--gap-xl);
}
.starIcon {
  height: 16px;
  width: 16px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
  min-height: 16px;
}
.button1 {
  position: relative;
  font-size: var(--body-strong-size);
  line-height: 100%;
  font-family: var(--body-strong);
  color: var(--background-default-secondary);
  text-align: left;
  display: inline-block;
  min-width: 89px;
}
.xIcon1 {
  height: 16px;
  width: 16px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
  min-height: 16px;
}
.button,
.cartSummary,
.pageProduct {
  display: flex;
  align-items: flex-start;
}
.button {
  cursor: pointer;
  border: 1px solid var(--background-brand-default);
  padding: var(--padding-3xs) var(--padding-xl);
  background-color: var(--background-brand-default);
  align-self: stretch;
  border-radius: var(--radius-200);
  overflow: hidden;
  flex-direction: row;
  justify-content: center;
  gap: var(--space-200);
}
.cartSummary,
.pageProduct {
  justify-content: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.cartSummary {
  flex: 1;
  border: 1px solid var(--color-black);
  flex-direction: column;
  padding: var(--padding-9xl) var(--padding-10xl);
  gap: var(--gap-14xl);
  min-width: 349px;
}
.pageProduct {
  align-self: stretch;
  background-color: var(--background-default-default);
  flex-direction: row;
  padding: var(--space-1600) var(--padding-3xs) var(--space-1600)
    var(--space-1600);
  gap: var(--gap-34xl);
  min-width: 240px;
  text-align: left;
  font-size: var(--heading-size);
  color: var(--text-default-default);
  font-family: var(--body-strong);
}
@media screen and (max-width: 925px) {
  .b,
  .b1 {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
  .section {
    flex: 1;
  }
  .pageProduct {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 700px) {
  .section {
    gap: var(--space-1600);
    min-width: 100%;
  }
  .cartSummary,
  .pageProduct {
    gap: var(--gap-base);
    padding-top: var(--padding-xl);
    padding-bottom: var(--padding-xl);
    box-sizing: border-box;
  }
  .pageProduct {
    gap: var(--gap-7xl);
    padding-left: var(--space-1600);
    padding-top: var(--space-1600);
    padding-bottom: var(--space-1600);
  }
}
@media screen and (max-width: 450px) {
  .textHeading1 {
    font-size: var(--font-size-lgi);
    line-height: 23px;
  }
  .b,
  .b1 {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .cartTotals1,
  .subTotalValues,
  .subtotal {
    font-size: var(--font-size-lgi);
    line-height: 23px;
  }
  .subTotal {
    flex-wrap: wrap;
  }
  .changeAddress,
  .freeShipping,
  .shipping1,
  .shippingToCalifonia,
  .total1,
  .totalValues {
    font-size: var(--font-size-lgi);
    line-height: 23px;
  }
  .totalValue {
    flex-wrap: wrap;
  }
  .cartSummary {
    min-width: 100%;
  }
}
