.textHeading1 {
  margin: 0;
  position: relative;
  font-size: inherit;
  letter-spacing: -0.02em;
  line-height: 29px;
  font-weight: 600;
  font-family: inherit;
}
.textHeading {
  display: flex;
  flex-direction: row;
}
.cardGridReviews,
.productCards,
.textHeading {
  align-items: flex-start;
  justify-content: flex-start;
}
.productCards {
  align-self: stretch;
  display: grid;
  flex-direction: row;
  gap: var(--gap-29xl);
  max-width: 100%;
  grid-template-columns: repeat(3, minmax(247px, 1fr));
}
.cardGridReviews {
  width: var(--device-width);
  background-color: var(--background-default-default);
  display: flex;
  flex-direction: column;
  padding: var(--space-1600) var(--padding-34xl) var(--space-1600)
    var(--space-1600);
  box-sizing: border-box;
  gap: var(--space-1200);
  max-width: 104%;
  flex-shrink: 0;
  text-align: left;
  font-size: var(--heading-size);
  color: var(--text-default-default);
  font-family: var(--body-strong);
}
.menu16Icon {
  height: 20px;
  width: 20px;
  position: relative;
}
.iconButton,
.menu16Icon,
.x16Icon {
  overflow: hidden;
  flex-shrink: 0;
}
.iconButton {
  height: 44px;
  width: 44px;
  border-radius: var(--scale-06);
  background-color: var(--background-default-secondary);
  border: 1px solid var(--border-default-default);
  box-sizing: border-box;
  display: none;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--space-300) var(--padding-2xs);
}
.x16Icon {
  height: 20px;
  width: 20px;
  position: relative;
}
.block,
.iconButton1 {
  display: none;
  flex-direction: row;
  align-items: center;
}
.iconButton1 {
  height: 44px;
  width: 44px;
  border-radius: var(--scale-06);
  background-color: var(--background-default-secondary);
  border: 1px solid var(--border-default-default);
  box-sizing: border-box;
  overflow: hidden;
  flex-shrink: 0;
  justify-content: center;
  padding: var(--space-300) var(--padding-2xs);
}
.block {
  width: 226px;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.habitualStoreCheckoutPage {
  width: 100%;
  position: relative;
  background-color: var(--background-default-default);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 925px) {
  .productCards {
    justify-content: center;
    grid-template-columns: repeat(2, minmax(247px, 428px));
  }
}
@media screen and (max-width: 700px) {
  .productCards {
    gap: var(--gap-5xl);
    grid-template-columns: minmax(247px, 1fr);
  }
  .cardGridReviews {
    gap: var(--space-1200);
    padding-top: var(--space-1600);
    padding-bottom: var(--space-1600);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .textHeading1 {
    font-size: var(--font-size-lgi);
    line-height: 23px;
  }
}
