.textHeading1 {
  margin: 0;
  position: relative;
  font-size: inherit;
  letter-spacing: -0.02em;
  line-height: 29px;
  font-weight: 600;
  font-family: inherit;
}
.reviewCards,
.textHeading {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.reviewCards {
  width: 725px;
  flex: 1;
  overflow-x: auto;
  gap: var(--gap-29xl);
  max-width: 100%;
}
.cardGridReviews,
.content,
.footerWrapper {
  display: flex;
  align-items: flex-start;
  box-sizing: border-box;
}
.cardGridReviews {
  width: var(--device-width);
  height: 645px;
  background-color: var(--background-default-default);
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--space-1600) var(--padding-34xl) var(--space-1600)
    var(--space-1600);
  gap: var(--space-1200);
  max-width: 153%;
  flex-shrink: 0;
}
.content,
.footerWrapper {
  max-width: 100%;
}
.content {
  width: 809px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0 0 0 var(--padding-xl);
  text-align: left;
  font-size: var(--heading-size);
  color: var(--text-default-default);
  font-family: var(--body-strong);
}
.footerWrapper {
  width: 849px;
  flex-direction: row;
  justify-content: flex-end;
  padding: 0 60px;
}
.habitualProductPage {
  width: var(--device-width);
  position: relative;
  background-color: var(--background-default-default);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  padding: var(--padding-xl) 0 75px;
  box-sizing: border-box;
  gap: 328px;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1200px) {
  .footerWrapper {
    padding-left: var(--padding-11xl);
    padding-right: var(--padding-11xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .cardGridReviews,
  .footerWrapper {
    padding-left: var(--space-1600);
    padding-right: var(--space-1600);
    box-sizing: border-box;
  }
  .footerWrapper {
    padding-left: var(--padding-11xl);
    padding-right: var(--padding-11xl);
  }
  .habitualProductPage {
    gap: 164px;
  }
}
@media screen and (max-width: 750px) {
  .reviewCards {
    gap: var(--gap-5xl);
  }
  .cardGridReviews {
    padding-top: var(--space-1600);
    padding-bottom: var(--space-1600);
    box-sizing: border-box;
  }
  .footerWrapper {
    padding-left: var(--padding-11xl);
    padding-right: var(--padding-11xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .textHeading1 {
    font-size: var(--font-size-lgi);
    line-height: 23px;
  }
  .cardGridReviews {
    gap: var(--space-1200);
  }
  .footerWrapper {
    padding-left: var(--padding-11xl);
    padding-right: var(--padding-11xl);
    box-sizing: border-box;
  }
  .habitualProductPage {
    gap: 82px;
  }
}
