.productImageIcon {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: contain;
}
.image {
  height: 88px;
  width: 78px;
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: var(--padding-base-5) var(--padding-7xs);
  box-sizing: border-box;
  background-image: url(/public/image3@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.controller,
.dualsenseWireless {
  margin: 0;
}
.textHeading1 {
  position: relative;
  letter-spacing: -0.02em;
  line-height: 120%;
  font-weight: 600;
}
.textHeading {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.tag1,
.xIcon {
  height: 16px;
  position: relative;
}
.tag1 {
  flex: 1;
  line-height: 100%;
  display: inline-block;
}
.xIcon {
  width: 16px;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.tag {
  width: 44px;
  border-radius: var(--radius-200);
  background-color: var(--background-positive-secondary);
  display: none;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--space-200);
  box-sizing: border-box;
  gap: var(--space-200);
}
.placeholder,
.placeholder1 {
  position: relative;
  letter-spacing: -0.02em;
  line-height: 120%;
  display: inline-block;
  min-width: 7px;
}
.placeholder1 {
  min-width: 28px;
}
.price1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.month {
  width: 52px;
  position: relative;
  font-size: var(--label-14px-regular-size);
  line-height: 140%;
  display: none;
}
.textPrice {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: flex-start;
  gap: var(--space-200);
  font-size: var(--font-size-3xs);
  color: var(--text-default-default);
}
.price,
.title {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.price {
  justify-content: center;
  gap: var(--space-100);
  color: var(--text-positive-on-positive-secondary);
}
.title {
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.productDescription {
  flex: 1;
  position: relative;
  line-height: 140%;
}
.body,
.text {
  align-self: stretch;
  justify-content: flex-start;
}
.text {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  font-size: var(--font-size-3xs);
  color: var(--text-default-secondary);
}
.body {
  flex-direction: column;
  padding: 0 var(--padding-165xl) 0 0;
  gap: var(--space-400);
}
.body,
.column,
.section,
.section1 {
  display: flex;
  align-items: flex-start;
}
.column {
  flex: 1;
  flex-direction: column;
  justify-content: center;
  min-width: 266px;
  max-width: 100%;
}
.section,
.section1 {
  justify-content: flex-start;
}
.section1 {
  width: 499px;
  flex-direction: row;
  flex-wrap: wrap;
  gap: var(--gap-2xs);
  max-width: 159%;
  flex-shrink: 0;
}
.section {
  align-self: stretch;
  flex-direction: column;
  padding: 0;
}
.starIcon {
  height: 16px;
  width: 16px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.button1 {
  position: relative;
  line-height: 100%;
}
.xIcon1 {
  height: 16px;
  width: 16px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.button,
.pageProduct {
  align-self: stretch;
  display: flex;
}
.button {
  border-radius: var(--radius-200);
  background-color: var(--background-brand-default);
  border: 1px solid var(--background-brand-default);
  overflow: hidden;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-3xs) var(--padding-xl);
  gap: var(--space-200);
  font-size: var(--font-size-3xs);
  color: var(--background-default-secondary);
}
.pageProduct {
  background-color: var(--background-default-default);
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--space-600);
  box-sizing: border-box;
  gap: var(--space-600);
  min-width: 240px;
  text-align: left;
  font-size: var(--body-strong-size);
  color: var(--text-default-default);
  font-family: var(--body-strong);
}
